@keyframes span {
  0% {
    width: 3rem;
  }
  25% {
    width: 6rem;
  }
  50% {
    width: 10rem;
  }
  75% {
    width: 6rem;
  }
  100% {
    width: 3rem;
  }
}
.main_wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  max-width: 180rem;
  height: 100vh;
  margin: 0 auto;
  font-family: 'Roboto Condensed', sans-serif;
}
@media only screen and (max-width: 900px) {
  .main_wrapper {
    flex-direction: column-reverse;
    height: 100%;
  }
}
.description {
  flex-basis: 37%;
  background: #FDFDFD;
  padding: 5rem;
}
@media only screen and (max-width: 900px) {
  .description {
    padding: 0 5rem 5rem 5rem;
  }
}
@media only screen and (max-width: 600px) {
  .description {
    padding: 0 5rem 5rem 3rem;
  }
}
.description_navbar {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #222222;
  font-size: 1.3rem;
  height: 2rem;
  transition: transform 0.5s;
}
@media only screen and (max-width: 900px) {
  .description_navbar {
    display: none;
  }
}
.description_navbar.button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 3.6rem;
  height: 3.6rem;
  border-radius: 10rem;
  cursor: pointer;
  transition: all 0.05s;
}
.description_navbar.button:hover {
  background: rgba(167, 166, 166, 0.1);
}
.description_navbar.button.bar {
  height: 2px;
  width: 1.8rem;
  background: #222222;
}
.description_navbar.button.bar:not(:last-child) {
  margin-bottom: 3px;
}
.description_navbar.wrapper {
  transform: scaleX(1) translateX(50px);
  transform-origin: left;
}
.description_navbar.item {
  margin-right: 2.5rem;
  text-transform: uppercase;
  font-weight: 600;
}
.description_title {
  font-size: 4rem;
  color: #222222;
  text-transform: capitalize;
  text-align: center;
  margin: 3rem 5rem 0 auto;
}
@media only screen and (max-width: 900px) {
  .description_title {
    margin: 0 auto;
  }
}
.description_subtitle {
  font-size: 1.6rem;
  color: #222222;
  text-align: center;
  margin: 0 6rem 7rem auto;
  font-weight: 500;
}
@media only screen and (max-width: 900px) {
  .description_subtitle {
    margin: 0 auto 7rem auto;
  }
}
.description_specification {
  display: flex;
  font-size: 1.4rem;
  text-transform: capitalize;
  margin: 0 0 2rem 0;
}
@media only screen and (max-width: 900px) {
  .description_specification {
    margin: 2rem auto;
  }
}
.description_specification_title {
  color: #a7a6a6;
  margin: 0 5.5rem 0 0;
  display: flex;
  flex-basis: 7.5rem;
}
@media only screen and (max-width: 900px) and (min-width: 600px) {
  .description_specification_title {
    flex-basis: 15rem;
    margin: 0 5.5rem 0 4rem;
  }
}
@media only screen and (max-width: 600px) {
  .description_specification_title {
    margin: 0 3.5rem 0 0;
  }
}
.description_specification_value {
  color: #222222;
}
.description_specification_value.wrapper {
  display: flex;
}
.description_specification_value.wrapper > p:first-child {
  margin-right: 5rem;
  width: 7rem;
}
.description_footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 8rem 0 0 0;
}
@media only screen and (max-width: 600px) {
  .description_footer {
    flex-direction: column;
  }
}
.description_footer_item {
  font-size: 1.3rem;
  color: #a7a6a6;
}
@media only screen and (max-width: 600px) {
  .description_footer_item {
    margin: 1rem auto 0 auto;
  }
}
@media only screen and (max-width: 900px) {
  .craousel_controller {
    visibility: hidden;
  }
}
.craousel_controller.left {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 3rem -5rem 0 0;
}
.craousel_controller.right {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 10rem 0 0 0;
}
.craousel_controller_btn {
  height: 3.6rem;
  width: 3.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
@media only screen and (max-width: 1080px) {
  .craousel_controller_btn {
    visibility: hidden;
  }
}
.craousel_controller_btn.left {
  background: #222222;
}
.craousel_controller_btn.left:hover > .left-arrow {
  fill: #FDFDFD;
}
.craousel_controller_btn.right {
  background: #FDFDFD;
  position: absolute;
  top: 10rem;
  left: 0;
  z-index: 999999;
}
.craousel_controller_btn.right > * {
  transform: rotate(180deg);
}
.craousel_controller_btn.right:hover > .left-arrow {
  fill: #222222;
}
.picture {
  display: flex;
  flex-direction: column;
  flex-basis: 63%;
  position: relative;
}
.picture_large {
  height: 90rem;
  position: relative;
  background-color: #7d7c92;
  margin: 0 auto;
}
.picture_large-slider {
  position: absolute;
  top: 0;
  bottom: 0;
  transition: all 0.4s;
  width: 100%;
}
@media only screen and (max-width: 900px) {
  .picture_large-slider {
    display: flex;
    width: 400%;
  }
}
.picture_large-item {
  transition: all 0.4s;
  height: 100%;
  width: 100%;
  overflow: hidden;
  object-fit: fill;
  object-position: top center;
  display: flex;
  justify-content: center;
}
@media only screen and (max-width: 900px) {
  .picture_large-item {
    flex: 1;
  }
}
.picture_large-item.secondary {
  display: none;
}
@media only screen and (max-width: 900px) {
  .picture_large-item.secondary {
    display: block;
  }
}
.picture_large-item--blinker {
  background-color: #FEFEFE;
  opacity: 0.2;
  animation-duration: 0.4s;
  height: 100%;
  width: 0;
  z-index: 999999;
  position: absolute;
  top: 0;
  content: none;
}
.picture_large-item--blinker.left {
  border-top-right-radius: 100%;
  border-bottom-right-radius: 100%;
  left: 0;
}
.picture_large-item--blinker.right {
  border-top-left-radius: 100%;
  border-bottom-left-radius: 100%;
  right: 0;
}
.picture_large-item--img {
  height: 100%;
  width: 100%;
  z-index: 999;
}
.picture_large-spinner {
  position: absolute;
  top: 20%;
}
.picture_intro {
  display: flex;
  flex-direction: row;
  flex-basis: auto;
  background: #a7a6a6;
  align-self: stretch;
  flex-shrink: 0;
  flex-grow: 1;
}
@media only screen and (max-width: 480px) {
  .picture_intro {
    flex-direction: column;
  }
}
.picture_intro_img {
  display: flex;
  flex-basis: 35%;
  z-index: 99;
}
@media only screen and (max-width: 480px) {
  .picture_intro_img {
    flex-basis: auto;
  }
}
.picture_intro_img > img {
  height: 25rem;
  width: 18rem;
  margin: -10rem 5rem 5rem 5rem;
  border-radius: 2px;
}
@media only screen and (max-width: 480px) {
  .picture_intro_img > img {
    margin: -10rem 3rem 2rem 3rem;
  }
}
.picture_intro_txt {
  flex-basis: 65%;
  padding: 2rem 7rem 0 0;
  font-size: 1.5rem;
  color: #222222;
  font-weight: 500;
  text-transform: capitalize;
}
@media only screen and (max-width: 1080px) {
  .picture_intro_txt {
    padding: 2rem 2rem 2rem 0;
  }
}
@media only screen and (max-width: 480px) {
  .picture_intro_txt {
    flex-basis: auto;
    padding: 0 2rem 2rem 3rem;
  }
}
.sidedrawer {
  height: 4rem;
  width: 4rem;
  position: fixed;
  left: 3rem;
  top: 3rem;
  display: none;
  z-index: 999;
}
@media only screen and (max-width: 900px) {
  .sidedrawer {
    display: block;
  }
}
.left {
  transform-origin: left;
}
.right {
  transform-origin: right;
}
.left-arrow {
  fill: #a7a6a6;
  height: 1.6rem;
  width: 1.6rem;
}
.shapes-and-symbols {
  fill: #222222;
  height: 1.6rem;
  width: 1.6rem;
  margin: 0 0 0 1.7rem;
}
.shapes-and-symbols-1 {
  fill: #222222;
  height: 1.6rem;
  width: 1.6rem;
  margin: 0 0 0 10.3rem;
}
